/*===== VARIABLES CSS =====*/
:root{
    --header-height: 3rem;
    --nav-width: 68px;

    /*===== Colors =====*/
    --first-color: #293D7F;
    --first-color-light: #AFA5D9;
    // --white-color: #F7F6FB;
    --sub-color: #B6CEFC;
    /*===== z index =====*/
    --z-fixed: 100;
}

/*===== BASE =====*/
*,::before,::after{
    box-sizing: border-box;
}

body{
    position: relative;
    // margin: var(--header-height) 0 0 0;
    // padding: 0 1rem;
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    transition: .5s;
}

a{
    text-decoration: none;
}

/*===== HEADER =====*/
.header{
    width: 100%;
    height: var(--header-height);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
    background-color: var(--white-color);
    z-index: var(--z-fixed);
    transition: .5s;

    &__toggle{
        color: var(--first-color);
        font-size: 1.5rem;
        cursor: pointer;
    }
    &__img{
        width: 35px;
        height: 35px;
        display: flex;
        justify-content: center;
        border-radius: 50%;
        overflow: hidden;

        & img{
            width: 40px;
        }
    }
}

/*===== NAV =====*/
.l-navbar{
    position: fixed;
    top: 0;
    left: -30%;
    width: var(--nav-width);
    height: 100vh;
    background-color: var(--first-color);
    padding: 0rem 1rem 0 0;
    transition: .5s;
    z-index: var(--z-fixed);

}
.nav{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-right: -10px;
    overflow: hidden;

    &__logo, &__link{
        display: grid;
        grid-template-columns: max-content max-content;
        align-items: center;
        column-gap: 1rem;
        padding: 0rem 0 0.5rem 1.9rem;
        padding-right: 60px;
    }
    &__logo{
        margin-bottom: 2rem;
    }

    &__link{
        position: relative;
        color: var(--first-color-light);
        margin-bottom: 1rem;
        margin-left: -5px;
        transition: .3s;
        
        &:hover{
            color: #F7F6FB;
            text-decoration: none;
        }

    }

    &__icon{
        margin-right: 10px;
        font-size: 1.25rem;
    }
    & img {
        margin-left: -30px;
        margin-right: -20px;
        width: 220px;
        height: 150px;
    }
    a:hover{
    background: #64A3C6;
    }
}

/*Show navbar movil*/
.show{
    left: 0;
}

/*Add padding body movil*/
.body-pd{
    padding-left: calc(var(--nav-width) + 1rem);
}

/*Active links*/
.act{
    color: #F7F6FB;
    &::before{
        content: '';
        position: absolute;
        left: 0;
        width: 2px;
        height: 32px;
    }
}

.active{
    grid-template-columns: 20px max-content 1fr;
    & li{
        list-style: none;
        width: 200px;
    }
    
    // &_link{
