    //   font-size: var(--small-font-size);
      
    //   &:hover{
    //     color: var(--white-color);
    //   }
    // }
}
.active_link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.3rem;
    transition: .5s;
}
.active_menu{
    margin:1rem 0 0 1rem;
    display: none;
}
.active_item{
    margin: 1rem 0;
}
.active:hover > .active_menu{
    display: block;
}
.active:hover .active_list{
    transform: rotate(180deg);
}

